import { MoreVertical } from "react-feather";
import { ReactComponent as Share } from "@src/assets/images/icons/share.svg";
import {
    UncontrolledDropdown,
    DropdownItem,
    DropdownMenu,
    DropdownToggle,
} from "reactstrap";
import { FormattedMessage } from "react-intl";
import moment from "moment";

import DesiredCellLayout from "@components/cell-grid/CustomCellLayout";
import { Link } from "react-router-dom";

const COLUMN_WIDTH = {
    ID: "10%",
    ADDRESS: "50%",
    DATE: "10%",
    NOTE: "30%",
    ACTION: "1%",
}

export const COLUMN_CONFIG = [
    {
        dataIndex: "id",
        title: (
            <div>
                <FormattedMessage id="id" />
            </div>
        ),
        fixed: true,
        className: "",
        render: (id) => {
            const resId = Object.values(id).toString();
            return <Link to={`/view/${resId}`}>#{ resId.padStart(6,0)}</Link>;
        },
        onCell: (_) => ({
            style: {
                color: "blue",
                textDecoration: "underline",
            },
        }),
        width: COLUMN_WIDTH.ID,
        visible: true,
    },
    {
        dataIndex: "address",
        title: (
            <div className="pl-1 text-uppercase">
                <FormattedMessage id="address" />
            </div>
        ),
        fixed: false,
        className: "pl-0",
        render: (address) => DesiredCellLayout({item: address, layout: {cols: 1, isMultipleRow: false, reverseData: false}}),
        onCell: (_) => ({
            className: "pl-1",
        }),
        width: COLUMN_WIDTH.ADDRESS,
        visible: true,
    },
    {
        dataIndex: "date",
        title: (
            <div className="text-uppercase">
                <FormattedMessage id="alertDate" />
            </div>
        ),
        fixed: false,
        className: "pl-0",
    //   render: (land) => DesiredCellLayout({ item: land, layout: { cols: 2, isMultipleRow: true, reverseData: false } }),
        render: (date) => {
            const validDate = Object.values(date)[0];
            return validDate && moment(validDate).format("DD/MM/YYYY");
        },
        onCell: (_) => ({}),
        width: COLUMN_WIDTH.DATE,
        visible: true,
    },
    {
        dataIndex: "note",
        title: (
            <div className="text-uppercase">
                <FormattedMessage id="content" />
            </div>
        ),
        fixed: false,
        className: "pl-0",
        // render: (house) => DesiredCellLayout({item: house, layout: {cols: 2, isMultipleRow: true, reverseData: false}}),
        render: (note) => Object.values(note).toString(),
        onCell: (_) => ({}),
        width: COLUMN_WIDTH.NOTE,
        visible: true,
    }
];

export const COLUMN_DATA_CONFIG = {
    columns: [
        "id",
        "address",
        "date",
        "note",
    ],
    columnGroup: [
        ["realEstateRawId"],
        [
            "realEstateLocationHomeNumber",
            "areaStreetName",
            "areaWardName",
            "areaDistrictName",
            "areaProvinceName",
            "areaCountryName",
        ],
        [
            "approveDate"
        ],
        [
            "realEstateNote"
        ]
    ],
};

const DropdownItems = ({ record }) => (
    <>
        <DropdownItem
            tag="a"
            href="/"
            className="w-100"
            onClick={(e) => {
                e.preventDefault();
                navigator.clipboard.writeText(
                    window.location.href + "/" + Object.values(record.id)
                );
            }}
        >
            <Share size={15} />
            <span className="align-middle ml-50">
                <FormattedMessage id="shareRes" />
            </span>
        </DropdownItem>
    </>
);
