import { memo, useEffect } from "react";
import PropTypes from "prop-types";
import CustomPagination from "@components/customPaginationV1";
import CustomSelectDropdown from "@components/customSelectDropdown";
import { CheckCircle } from "react-feather";
import {ReactComponent as Alert} from "@src/assets/images/icons/alert-icon.svg";
import { injectIntl, FormattedMessage } from "react-intl";
import { Button, Table, notification } from "antd";

import RealEstateServices from "../../../services/post";

import "antd/dist/antd.css";
import "./AlertResDatatable.scss";
import { DropdownItem } from "reactstrap";
import { REQUEST_CONFIG } from "../../../constants/request";
import { ENDPOINTS } from "../../../constants/endpoints";
import { ReactComponent as FilterIcon } from "@src/assets/images/icons/filter.svg";
import Sidebar from "@components/sidebar";
import PickedFilter from "@components/filters/filterComponents/PickedFilter";
import { countNotEmptyProperties } from "@src/helper/object";

import "antd/dist/antd.css";

const INITIAL_PROPS = {
    tableHeader: [],
    tableData: [],
    selectedRows: [{ id: 1, address: "" }],
    setSelectedRows: () => {},
    totalRecords: 0,
    totalPages: 1,
    currentPage: 0,
    setCurrentPage: () => {},
};

const RawResDatatable = ({
    loading,
    tableData,
    selectedRows,
    setSelectedRows,
    totalRecords,
    totalPages,
    currentPage,
    setCurrentPage,
    visibleColumns,
    setVisibleColumns,
    sidebarVisibility,
    setSidebarVisibility,
    selectedFilterValues,
    setSelectedFilterValues,
    intl,
} = INITIAL_PROPS) => {
    // rowSelection object indicates the need for row selection
    const rowSelection = {
        onChange: (_, sltRows) => {
            setSelectedRows(sltRows);
        },
    };

    function moveToPicked() {
        // TODO: call api to move selected res to picked list
        selectedRows.forEach((row) =>
            notification.info({
                message:
                    intl.formatMessage(
                        { id: "ResXMovedTo" },
                        { x: `${row.id} | ${row.address}` }
                    ) + intl.formatMessage({ id: "alertRes" }),
            })
        );
    }

    function toggleSidebar() {
        setSidebarVisibility(!sidebarVisibility);
    }

    return (
      <div className="ar-border-radius-24 ar-background-white">
          <div className="ar-border-radius-24-top ar-background-yellow-3 ar-table-header d-flex flex-column">
              <div className="d-flex align-items-baseline justify-content-between">
                  <div className="d-flex align-items-baseline">
                      <Alert size={20} color="#323759" />
                      <span className="ml-2 ar-table-header-title ar-color-main">
                          <FormattedMessage id="alertRes" />
                      </span>
                      <span className="ml-1 ar-table-header-title-sub ar-color-main">
                          <FormattedMessage
                              id="XTotal"
                              values={{ x: totalRecords }}
                          />{" "}
                          {}
                      </span>
                  </div>
                  {/* <div className="d-flex align-items-center">
                      <div className="ar-input-wrapper">
                          <FilterIcon color="white !important" />{" "}
                          <input
                              className="input-box"
                              value={selectedFilterValues?.searchText}
                              onChange={({ target: { value } }) =>
                                  setSelectedFilterValues({
                                      ...selectedFilterValues,
                                      searchText: value,
                                  })
                              }
                          />
                      </div>

                      <div className="ar-button-wrapper">
                          <Button
                              className="button-box"
                              onClick={toggleSidebar}
                          >
                              <FilterIcon className="mr-1" />{" "}
                              <FormattedMessage id="filter" />(
                              {countNotEmptyProperties(selectedFilterValues)})
                          </Button>
                      </div>
                  </div> */}
              </div>
              <div className="d-flex align-items-center ar-color-main ar-table-header-subtitle">
              </div>
          </div>
          <div className="d-flex flex-column align-items-center">
                <Table
                  id="alertResTable"
                  className="w-100"
                  columns={visibleColumns.filter((item) => item.visible)}
                  dataSource={tableData}
                  rowKey={(record) =>
                      Object.values(Object.values(record)[0])[0]
                  }
                //   rowSelection={rowSelection}
                  pagination={false}
                  sticky
                  scroll={{ x: true }}
                  loading={loading}
              />
              <CustomPagination
                  totalPages={totalPages}
                  currentPage={currentPage}
                  setCurrentPage={setCurrentPage}
              />
          </div>
          <Sidebar
              open={sidebarVisibility}
              toggleSidebar={toggleSidebar}
              closeBtn={false}
              width="500"
              title=""
          >
              <PickedFilter
                  values={selectedFilterValues}
                  setValues={setSelectedFilterValues}
              />
          </Sidebar>
      </div>
  );
};

export default injectIntl(memo(RawResDatatable));

RawResDatatable.propTypes = {
    tableHeader: PropTypes.arrayOf(PropTypes.object),
    tableData: PropTypes.arrayOf(PropTypes.object),
    selectedRows: PropTypes.arrayOf(PropTypes.object),
    setSelectedRows: PropTypes.func,
    totalRecords: PropTypes.number,
    totalPages: PropTypes.number,
    currentPage: PropTypes.number,
    setCurrentPage: PropTypes.func,
};

RawResDatatable.defaultProps = INITIAL_PROPS;
